var browserConf = require('./conf-browser');
var isWeb    = typeof WXEnvironment === 'object' && WXEnvironment.platform == 'Web';

module.exports = {
    sendRequest: function (url) {
        var wpo = this;

        if (url.indexOf('https:') != 0 && url.indexOf('//') == 0) {
            url = 'https:' + url;
        }

        // weex
        if (isWeb) {//window window.navigator,WXEnvironment
            browserConf.sendRequest(url);
        } else {
            /**
             * weex fetch
             * : <http://alibaba.github.io/weex/doc/modules/stream.html>
             *
             * @param options {Object}
             * @param callback {Function}
             * @param progressCallback {Function}
             * @description
             *
             *  ****: 3,weex requestwork
             *
             */
            wpo.config.request({
                method: 'GET',
                url   : url,
                type  : 'json'
            }, function (response) {
                // debug
                if (wpo.debug && wpo.config.toast) {
                    wpo.config.toast({
                        'message' : 'log report success, ' + url + ', ' + JSON.stringify(response),
                        'duration': 5
                    });
                }
            }, function (response) {
            });
        }
    },

    getCookie: function (wpo) {
        return wpo.config.cookie;
    },

    getSpmId: function () {
        return this.spmId;
    }
};


