(function (win) {
    var browserConf = require('./conf-browser');

    var wpo      = win.__WPO || {},
        status   = 2,
        isLoaded = false;

    // env
    wpo.env = 'browser';

    require('./core')(wpo, win, browserConf);

    var exec = function () {
        require('./sampling')(wpo);
        require('./apis')(wpo);
        require('./browser-performance')(wpo, win, isLoaded, browserConf);
    };

    if (wpo.config.dynamic) {
        //
        // log.js
        //
        if (!(status = require('./server-config')(wpo))) {
            require('./api-await')(wpo, function () {
                exec();
                if (wpo.reloaded) {
                    wpo.reloaded();
                }
            });
            return;
        }
    }

    if (status == 2) {
        wpo.on(win, 'load', function () {
            wpo.ready();
        }, true);
    }
    else {
        wpo.on(win, 'load', function () {
            isLoaded = true;
        });
    }

    exec();
})(window);

module.exports = window.__WPO;


