module.exports = function (wpo, root, conf) {
    var cookies = {};
    var count   = 0;
    var uid, timer;

    var config = {
        // 
        imgUrl : '//retcode.taobao.com/r.png?',
        // 
        sample : 10,
        modVal : 1,
        // 
        dynamic: false,
        retCode: {}
    };

    var sendRequest = conf.sendRequest;

    var _send = function () {
        var params, obj;

        while (params = core.dequeue()) {
            obj = core.extend({
                uid     : uid,
                userNick: wpo.getNick(),
                times   : params.times ? params.times : 1,
                _t      : ~new Date() + (count++).toString()
            }, params);

            // spm
            if (!obj.spm) {
                obj.spm = wpo.getSpmId();
            }

            if (!obj.spm) {
                break;
            }

            if (wpo.debug && typeof window === 'object' && window.console) {
                console.log(obj);
            }

            sendRequest.call(wpo, config.imgUrl + core.query.stringify(obj));
        }

        timer = null;
    };

    var _wait = function (_clear) {
        if (_clear && timer) {
            clearTimeout(timer);
            _send();
        }
        if (!timer) {
            timer = setTimeout(_send, 1000);
        }
    };

    var core = {
        _key: 'wpokey',

        // 
        ver: '<%= pkg.version %>',

        // dynamically updates itself without queue
        requestQueue: wpo.requestQueue || [],

        /**
         * cookie
         *
         * @param name
         * @returns {*}
         */
        getCookie: function (name) {
            var reg,
                matches,
                cookie = '';

            if (!cookies[name]) {
                reg = new RegExp(name + '=([^;]+)');

                //
                // to make it compatible with nodejs
                //
                try {
                    cookie = conf.getCookie(this);
                }
                catch (e) {

                }

                matches = reg.exec(cookie);
                if (matches) {
                    cookies[name] = matches[1];
                }
            }

            return cookies[name];
        },

        /**
         * cookie
         *
         * @param key
         * @param value
         * @param expires
         * @param domain
         * @param path
         */
        setCookie: function (key, value, expires, domain, path) {
            var str = key + '=' + value;
            if (domain) {
                str += ('; domain=' + domain);
            }
            if (path) {
                str += ('; path=' + path);
            }
            if (expires) {
                str += ('; expires=' + expires);
            }
            document.cookie = str;
        },

        /**
         * 
         *
         * @param target
         * @returns {*}
         */
        extend: function (target) {
            var args = Array.prototype.slice.call(arguments, 1);

            for (var i = 0, len = args.length, arg; i < len; i++) {
                arg = args[i];
                for (var name in arg) {
                    if (arg.hasOwnProperty(name)) {
                        target[name] = arg[name];
                    }
                }
            }

            return target;
        },

        /**
         * guid
         *
         * @returns {string}
         */
        guid: function () {
            return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
                var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
                return v.toString(16);
            });
        },

        /**
         * 
         *
         * @param params
         */
        send: function (params) {
            // wpo, e.g., dailywpo,
            if (params && !wpo.config.disabled) {
                var spm = core.getSpmId();

                // spm,spm,spm
                // spm,SPAsetConfigspm
                if (!params.spm && spm) {
                    params.spm = spm;
                }

                this.queue(params);
            }
        },

        query: {
            /**
             * url
             *
             * @param params
             * @returns {string}
             */
            stringify: function (params) {
                var arr = [];
                var str = '';

                // encodeURIComponent
                try {
                    for (var name in params) {
                        if (params.hasOwnProperty(name) && params[name] !== undefined) {
                            arr.push(name + '=' + encodeURIComponent(params[name]));
                        }
                    }

                    if (arr.length > 0) {
                        str = arr.join('&');
                    }
                }
                catch (e) {
                }

                return str;
            },

            /**
             * url
             *
             * @param str
             * @returns {{}}
             */
            parse: function (str) {
                var pairs = str.split('&'),
                    obj   = {}, pair;

                // decodeURIComponent
                try {
                    for (var i = 0, len = pairs.length; i < len; i++) {
                        pair         = pairs[i].split('=');
                        obj[pair[0]] = decodeURIComponent(pair[1]);
                    }
                }
                catch (e) {
                }

                return obj;
            }
        },

        /**
         * spmId
         *
         * @returns {*}
         */
        getSpmId: function () {
            if (config.spmId) {
                return config.spmId;
            }
            else if (typeof conf.getSpmId === 'function') {
                return conf.getSpmId.call(this);
            }

            return 0;
        },

        /**
         * 
         *
         * @param el
         * @param type
         * @param func
         * @param isRemoving
         */
        on: function (el, type, func, isRemoving) {
            if (el.addEventListener) {
                el.addEventListener(type,
                    isRemoving ? function () {
                        el.removeEventListener(type, func, false);
                        func();
                    } : func,
                    false);
            }
            else if (el.attachEvent) {
                el.attachEvent('on' + type, function () {
                    if (isRemoving) {
                        el.detachEvent('on' + type, arguments.callee);
                    }
                    func();
                });
            }
        },

        /**
         * taobao nick
         *
         * @returns {*}
         */
        getNick: function () {
            var nick = '';

            try {
                // nick
                if (wpo.config.nick) {
                    nick = wpo.config.nick;
                }
                // cookie
                else {
                    // PC,,cookie
                    nick = this.getCookie('_nk_') || this.getCookie('lgc') || this.getCookie('_w_tb_nick') || this.getCookie('sn') || '';
                    nick = decodeURIComponent(nick);
                }
            }
            catch (e) {
            }

            return nick;
        },

        /**
         * config
         * @param inConfig
         * @returns {*}
         */
        setConfig: function (conf) {
            if (conf && typeof conf !== 'object') {
                throw 'args of wpo.setConfig is not object';
                return;
            }

            // weex
            if (conf && conf.user && typeof conf.user.getUserInfo === 'function' && this.env === 'weex') {
                // nick
                conf.user.getUserInfo(function (resStr) {
                    try {
                        var res = JSON.parse(resStr);

                        core.extend(config, {
                            nick: res.info && res.info.nick
                        });
                    }
                    catch (e) {
                    }
                });
            }

            return core.extend(config, conf);
        },

        /**
         * spm,wpo
         *
         * @param spm
         * @returns {core}
         */
        spm: function (spm) {
            if (spm) {
                this.setConfig({
                    spmId: spm
                })
            }

            return this;
        },

        /**
         * 
         *
         * @param obj
         */
        dynamicConfig: function (obj) {
            var config = this.query.stringify(obj);

            try {
                localStorage.setItem(this._key, config);
            }
            catch (e) {
                this.setCookie(this._key, config, new Date(obj.expTime));
            }
            this.setConfig({
                sample: parseInt(obj.sample, 10)
            });
            this.ready();
        },

        /**
         * ready
         *
         * @param _immediately
         */
        ready: function (_immediately) {
            this._ready       = true;
            // var immediately = wpo.config ? (wpo.config.immediately ? true : false) : false;
            var immediately   = true;//
            this._immediately = _immediately || immediately;
            _wait();
        },

        /**
         * 
         *
         * @param obj
         */
        queue: function (obj) {
            var queue = this.requestQueue, compare;

            if (obj.type === 'jserror') {
                if (queue.length) {
                    compare = queue[queue.length - 1];

                    if (obj.msg === compare.msg) {
                        compare.times++;
                        return;
                    }
                }
                if (!obj.times) {
                    obj.times = 1;
                }
            }

            queue.push(obj);

            if (this._ready) {
                //
                // for nodejs
                //
                if (this._immediately && obj.type !== 'jserror') {
                    _send();
                }
                else {
                    _wait();
                }
            }
        },

        /**
         * ()
         *
         * @returns {T}
         */
        dequeue: function () {
            return this.requestQueue.shift();
        },

        /**
         * clear
         */
        clear: function () {
            _wait(true);
        }
    };

    core.uid = (uid = core.guid());
    core.config = core.setConfig(wpo.config);
    core.extend(wpo, core);

    root.__WPO = wpo;

    return wpo;
};


