import * as sdk from './request';
import wpo from '@ali/retcodelog';

let dingSDK = null;

function init(){
    wpo.setConfig({
        //1100%1010%1001%
        sample: 1,
        // spmId,,spm
        spmId: 'DINGDING_ICEPY'
    });
    wpo.log('DING WEB INTERFACE SDK START',1);
    return sdk;
}

if(!dingSDK){
    dingSDK = init();
}

export default dingSDK;


